import axios from 'axios';

const API_RV_BASE_URI = process.env.REACT_APP_BASE_URL;

const scheduleService = {
  async getClientsSchedule(user_id, date) {
    const URL = `${API_RV_BASE_URI}/api/schedule/date`;
    try {
      const { data, status } = await axios
        .get(URL, {
          params: { date: date },
          headers: { 'id-user': user_id },
        })
        .then((res) => ({ data: res.data, status: res.status }))
        .catch((err) => ({
          data: err.response.data,
          status: err.response.status,
        }));

      return { data, status };
    } catch (error) {
      return error;
    }
  },

  async addClientSchedule(
    user_id,
    client,
    procedure,
    date,
    time,
    price,
    contact,
    pacote,
    qtdTotalAtendimento,
  ) {
    const URL = `${API_RV_BASE_URI}/api/schedule/new`;
    try {
      const { data, status } = await axios
        .post(
          URL,
          {
            client: client,
            procedure: procedure,
            date: date,
            time: time,
            price: price,
            contact: contact,
            pacote: pacote || null,
            qtdTotalAtendimento: qtdTotalAtendimento,
          },
          { headers: { 'id-user': user_id } },
        )
        .then((res) => ({ data: res.data, status: res.status }))
        .catch((err) => ({
          data: err.response.data,
          status: err.response.status,
        }));

      return { data, status };
    } catch (error) {
      return error;
    }
  },

  async updateClientSchedule(
    user_id,
    id,
    client,
    procedure,
    date,
    time,
    price,
    contact,
    pacote,
    qtdTotalAtendimento,
    qtdAtendimento,
  ) {
    const URL = `${API_RV_BASE_URI}/api/schedule/update`;
    try {
      const { data, status } = await axios
        .put(
          URL,
          {
            id: id,
            client: client,
            procedure: procedure,
            date: date,
            time: time,
            price: price,
            contact: contact,
            pacote: pacote,
            qtdTotalAtendimento: qtdTotalAtendimento,
            qtdAtendimento,
          },
          { headers: { 'id-user': user_id } },
        )
        .then((res) => ({ data: res.data, status: res.status }))
        .catch((err) => ({
          data: err.response.data,
          status: err.response.status,
        }));

      return { data, status };
    } catch (error) {
      return error;
    }
  },

  async deleteClientSchedule(user_id, id) {
    const URL = `${API_RV_BASE_URI}/api/schedule`;
    try {
      const { data, status } = await axios
        .delete(URL, {
          params: { id },
          headers: { 'id-user': user_id },
        })
        .then((res) => ({ data: res.data, status: res.status }))
        .catch((err) => ({
          data: err.response.data,
          status: err.response.status,
        }));

      return { data, status };
    } catch (error) {
      return error;
    }
  },

  async finishClientSchedule(user_id, id) {
    const URL = `${API_RV_BASE_URI}/api/schedule/finish`;
    try {
      const { data, status } = await axios
        .post(
          URL,
          {
            id: id,
          },
          { headers: { 'id-user': user_id } },
        )
        .then((res) => ({ data: res.data, status: res.status }))
        .catch((err) => ({
          data: err.response.data,
          status: err.response.status,
        }));

      return { data, status };
    } catch (error) {
      return error;
    }
  },

  async getAllExpiredSchedules(id_user) {
    const URL = `${API_RV_BASE_URI}/api/schedule/expireds`;

    try {
      const { data, status } = await axios
        .get(URL, { headers: { 'id-user': id_user } })
        .then((res) => ({ data: res.data, status: res.status }))
        .catch((err) => ({
          data: err.response.data,
          status: err.response.status,
        }));

      return { data, status };
    } catch (error) {
      return error;
    }
  },

  async getScheduleByClient(user_id, client) {
    const URL = `${API_RV_BASE_URI}/api/schedule/client`;

    try {
      const { data, status } = await axios
        .get(URL, {
          params: { client },
          headers: { 'id-user': user_id },
        })
        .then((res) => ({ data: res.data, status: res.status }))
        .catch((err) => ({
          data: err.response.data,
          status: err.response.status,
        }));

      return { data, status };
    } catch (error) {
      return error;
    }
  },
};

export { scheduleService };
